<template>
    <div class="f-treetable panel panel-success">
        <div class="panel-heading" style="text-align:left;">
            CrossReferences
        </div>
        <div class="panel-body">
            <div class="yz-treetable">
                <table class="table table-bordered">
                    <colgroup>
                        <col width="100px">
                        <col>
                        <col width="300px">
                        <col width="300px">
                        <col width="300px">
                    </colgroup>
                    <thead>
                        <tr>
                            <th>序号</th>
                            <th>服务名称</th>
                            <th>地址</th>
                            <th>状态</th>
                            <th>操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, $index) in dealData">
                            <td style="text-align:left;">
                                <div class="table-tree-icon" :style="iconStyle(item)" @click="toggleChildren(item,$index)">
                                    <i class="yz-icon" :class="iconClass(item)"></i>
                                </div>
                            </td>
                            <td>{{item.serviceName}}</td>
                            <td>{{item.address}}</td>
                            <td>{{item.status}}</td>
                            <td>
                                <div class="btn-icon" title="删除" @click="handleDeleteRow(item)">
                                    <i class="icon iconfont icon-shanchu"></i>
                                </div>
                            </td>
                        </tr>
                        <tr v-if="dealData.length == 0">
                            <td colspan="5">
                                没有数据
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
var subNodeName = "subNodeList";

export default {
    name: "treeTable",
    componentName: "treeTable",
    props: {
        // data: Array
    },
    data() {
        return {
            dealData: [],
            data: []
        }
    },
    watch: {

    },
    mounted: function() {
        console.log('treeTable', this);
        this.getData();

    },
    methods: {
        getData: function() {
            var _this = this;
            this.$http({
                method: 'post',
                url: '/queryCrossReferences.do',
                data: {}

            }).then(function(resp) {
                 console.log(_this);

                var results = resp.data;
                // var result = [];
                // results.map(function(item,index){
                //     console.log('map',item);
                //     var obj = item;

                //     obj[subNodeName].map(function(childItem,childIndex){
                //         childItem[subNodeName] = childItem.referenceNodeList;
                //     })
                //     result.push(obj);
                // })
                _this.data = results;
                _this.dealData = results;

                _this.setParent(_this.data, 1, -1);
            })
        },

       handleDeleteRow: function(item) {
            var _this = this;
            var confirmOk = function() {
                _this.$http({
                    method: 'post',
                    url: '/delServiceNode.do',
                    data: {
                        serviceName:item.serviceName,
                        serviceNodeAddr:item.serviceNodeAddr
                    }

                }).then(function(resp) {
                    // console.log(resp);
                    _this.$message({
                        type:'success',
                        message:resp.status
                    })
                })
            }
            _this.$confirm('确定删除此', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                confirmOk();
            });
        },

        setParent: function(data, level, n) {
            // console.log(data, level, n);
            for (var a = data.length, r = 0; r < a; r++) {
                var item = data[r];
                item.checkbox = false;
                item.currentLevel = level;
                item.parentIndex = -1 == n ? null : n;
                if (item[subNodeName] && 0 != item[subNodeName].length) {
                    var l = -1 === n ? r : item.parentIndex + "-" + r;
                    this.setParent(item[subNodeName], level + 1, l)
                }
            }
        },
        iconStyle: function(e) {
            return {
                marginLeft: 10 * e.currentLevel + "px"
            }
        },
        iconClass: function(e) {
            return e[subNodeName] && 0 != e[subNodeName].length ? e.isOpen ? {
                "icon-open": !0
            } : {
                "icon-close": !0
            } : {
                "icon-default": !0
            }
        },
        toggleChildren: function(e, t) {
            // console.log("toggle", e, t);
            e[subNodeName] && 0 != e[subNodeName].length && (e.isOpen ? this.closeRow(e, t) : this.openRow(e, t))
        },
        openRow: function(e, t) {
            if (!e.isOpen) {
                for (var n = 0; n < e[subNodeName].length; n++) {
                    this.dealData.splice(t + n + 1, 0, e[subNodeName][n]);
                }
                e.isOpen = true;
            }
        },
        closeRow: function(row, index) {
            // console.log("-----", index, spliceLength);
            var spliceLength = 0;
            if (row.isOpen) {
                for (var i = index + 1, ilength = this.dealData.length; i < ilength; i++) {
                    var item = this.dealData[i];
                    if (item.currentLevel == row.currentLevel) {
                        spliceLength = i - index - 1;
                        break;
                    }
                    if (item.isOpen = true) {
                        item.isOpen = false;
                    }
                }
                // console.log("-----", index, spliceLength);
                this.dealData.splice(index + 1, spliceLength);
                // console.log(this.dealData)
                item.isOpen = false;
            }
            row.isOpen = false;
            
        },
        /**
         * 点击多选按钮 操作
         * @param  {[type]} item  [description]
         * @param  {[type]} index [description]
         * @return {[type]}       [description]
         */
        handleChangCheck:function(item,index){
            // console.log(item);
            this.setChildCheckBoxStatus(item,!item.checkbox);
            //return !item.checkbox;
        },

        setChildCheckBoxStatus(item,bool){
            if(item[subNodeName].length ===0 ){
                return;
            }
            for(var i=0,ilength=item[subNodeName].lenth;i<ilength;i++){
                var childItem = item[subNodeName][i];
                childItem.checkbox = bool;
                if(childItem[subNodeName] && childItem[subNodeName].length !== 0){
                    this.setChildCheckBoxStatus(childItem,bool)
                }
            }
        }
    }
}
</script>
<style>
.pc-header {
    height: 60px
}

.f-treetable .td {
    text-align: left;
}

.f-treetable .table-tree-icon {
    display: inline-block;
    margin-left: 10px;
}

.f-treetable .yz-icon {
    position: relative;
    display: inline-block;
    height: 18px;
    width: 18px;
}

.f-treetable .yz-icon.icon-close {
    background: url("/static/img/server/folderClose.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-open {
    background: url("/static/img/server/folderOpen.gif") 2px -1px no-repeat
}

.f-treetable .yz-icon.icon-default {
    background: url("/static/img/server/defaultLeaf.gif") 2px -1px no-repeat
}

.btn-icon {
    display: inline-block;
    width: 24px;
    height: 24px;
    text-align: center;
}

.btn-icon:hover {
    color: #20a0ff;
}
</style>



